import { useCallback, useState } from "react"

function useLogoLoadingError(logo: string | undefined) {
  const [hasLogoError, setHasLogoError] = useState(!logo)
  const setHasLogoErrorTrue = useCallback(() => setHasLogoError(true), [])

  return { hasLogoError, setHasLogoErrorTrue }
}

export { useLogoLoadingError }
